.container-categories-list {
  max-width: 335px;
  height: 228px;
  margin-bottom: 40px;
  overflow-y: scroll;
}

@media screen and (min-width: 768px) {
  .container-categories-list {
    width: 337px;
    height: 472px;
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1440px) {
  .container-categories-list {
    width: 356px;
    margin-bottom: 86px;
  }
}

.categories-list {
  letter-spacing: -.02em;
  color: var(--primary-text-color-theme-light);
  width: 100%;
  line-height: 1.12;
}

body.dark-theme .categories-list {
  color: var(--primary-text-color-theme-dark);
}

@media screen and (min-width: 768px) {
  .categories-list {
    font-size: 18px;
    display: inline-block;
  }

  ::-webkit-scrollbar-thumb {
    height: 167px;
  }
}

.categories-list__item {
  margin-bottom: 24px;
}

.categories-list__item:hover {
  cursor: pointer;
  color: var(--accent-color);
}

.categories-list__item.category-active {
  color: var(--accent-color);
  text-transform: uppercase;
  font-weight: 700;
}

.container-categories-list::-webkit-scrollbar {
  width: 8px;
}

.container-categories-list::-webkit-scrollbar-thumb {
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

body.dark-theme .container-categories-list::-webkit-scrollbar-thumb {
  background: rgba(17, 17, 17, .6);
}

